const map = new Map()
map.set("auth/user-not-found","El usuario no se encuentra registrado")
map.set("auth/wrong-password","Contrasea incorrecta")
map.set("auth/account-exists-with-different-credential","La cuenta ya estaba registrada usando un mtodo de ingreso distinto.")
map.set("auth/email-already-in-use","El correo electrnico ya est registrado")
export default function(errorCode){
    console.log(errorCode)
    var mensaje = map.get(errorCode)
    if(!mensaje){
        return "Ha ocurrido un error mientras se realizaba esta accin. Por favor, comunquese con soporte tcnico"
    }
    else{
        return mensaje
    }
}
